@font-face {
    font-family: 'ringbearermedium';
    src: url('fonts/ringm___-webfont.woff2') format('woff2'),
         url('fonts/ringm___-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;

}

body {
    margin: 0;
    height: 100vh;
    font-family: 'ringbearermedium';
}

main {
    background-image: url('img/arwen.jpg');
    background-size: contain;
    min-height: 100%;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
}

.card {
    width: 20vw;
    margin: 20px;
    text-align: center;
    height: 40vh;
    background-color: rgba(0, 0, 0, 0.3);
    box-shadow: 0 0 20px grey;
}

.card-header {
    border-bottom: 1px solid grey;
    height: 60px;
}

.card-header h2 {
    font-size: 1.3em;
    color: rgb(190, 201, 40);
}

.card-content {
    color: #eee;
}